"use client"

import type React from "react"
import { useState } from "react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { ChevronRight, Trash2, RotateCcw, Check, Upload, Plus } from "lucide-react"
import StepPreviewDialog from "@/components/step-preview-dialog"
export interface Item {
  id: string
  name: string
  color: string
  length: number
  width: number
  height: number
  weight: number
  count: number
  stackable: boolean
  turnable: boolean
  tiltable: boolean
}

interface ItemsPanelProps {
  onToggle: () => void
  items: Item[]
  onItemsChange: (items: Item[]) => void
}

export default function ItemsPanel({ onToggle, items, onItemsChange }: ItemsPanelProps) {
  const [selectedItem, setSelectedItem] = useState<string | null>(null)
  const [inputMode, setInputMode] = useState<"manual" | "file">("manual")
  const [previewOpen, setPreviewOpen] = useState(false)
  const [uploadedFileName, setUploadedFileName] = useState("")
  const [stlFile, setStlFile] = useState("")
  const [dimensions, setDimensions] = useState({ x: 0, y: 0, z: 0 })
  const handleConfirmItem = (data: {
    name: string
    length: number
    width: number
    height: number
    weight: number
  }) => {
    const newItem: Item = {
      id: `item-${Date.now()}`,
      name: data.name,
      color: `#${Math.floor(Math.random() * 16777215).toString(16)}`,
      length: data.length,
      width: data.width,
      height: data.height,
      weight: data.weight,
      count: 1,
      stackable: false,
      turnable: false,
      tiltable: false,
    }

    onItemsChange([...items, newItem])
    setSelectedItem(newItem.id)
  }



  const addManualItem = () => {
    const newItem: Item = {
      id: `item-${Date.now()}`,
      name: `Item ${items.length + 1}`,
      color: `#${Math.floor(Math.random() * 16777215).toString(16)}`,
      length: 100,
      width: 100,
      height: 100,
      weight: 10,
      count: 1,
      stackable: false,
      turnable: false,
      tiltable: false,
    }

    onItemsChange([...items, newItem])
    setSelectedItem(newItem.id)
  }

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (file) {
      console.log("Uploaded STEP file:", file.name)

      const reader = new FileReader()
      reader.onload = (e) => {
        console.log("File loaded, size:", file.size, "bytes")

        const newItem: Item = {
          id: `item-${Date.now()}`,
          name: file.name.replace(".step", "").replace(".stp", ""),
          color: `#${Math.floor(Math.random() * 16777215).toString(16)}`,
          length: 120,
          width: 80,
          height: 100,
          weight: 15,
          count: 1,
          stackable: false,
          turnable: false,
          tiltable: false,
        }

        onItemsChange([...items, newItem])
        setSelectedItem(newItem.id)
      }
      reader.readAsText(file)
    }
  }

  const updateItem = (itemId: string, field: keyof Item, value: any) => {
    onItemsChange(
      items.map((item) => (item.id === itemId ? { ...item, [field]: value } : item))
    )
  }

  const deleteItem = (itemId: string) => {
    onItemsChange(items.filter((item) => item.id !== itemId))
    if (selectedItem === itemId) {
      setSelectedItem(null)
    }
  }

  return (
    <div className="flex h-full flex-col">
      {/* Header */}
      <div className="flex items-center justify-between border-b border-gray-200 p-4">
        <h2 className="text-lg font-semibold text-gray-900">Items</h2>
        <Button variant="ghost" size="icon" onClick={onToggle}>
          <ChevronRight className="h-5 w-5" />
        </Button>
      </div>

      {/* Input Mode Selector and Action Buttons */}
      <div className="border-b border-gray-200 p-4">
        <div className="mb-3 flex gap-2">
          <Button
            variant={inputMode === "manual" ? "default" : "outline"}
            size="sm"
            className="flex-1"
            onClick={() => setInputMode("manual")}
          >
            Manual Input
          </Button>
          <Button
            variant={inputMode === "file" ? "default" : "outline"}
            size="sm"
            className="flex-1"
            onClick={() => setInputMode("file")}
          >
            Upload STEP File
          </Button>
        </div>

        {inputMode === "manual" ? (
          <Button onClick={addManualItem} className="w-full" size="sm">
            <Plus className="mr-2 h-4 w-4" />
            Add New Item
          </Button>
        ) : (
          <div className="relative">
            <input
              type="file"
              accept=".step,.stp"
              onChange={handleFileUpload}
              className="absolute inset-0 z-10 cursor-pointer opacity-0"
              id="step-file-input"
            />
            <Button className="w-full" size="sm" asChild>
              <label htmlFor="step-file-input" className="cursor-pointer">
                <Upload className="mr-2 h-4 w-4" />
                Upload STEP File (.step, .stp)
              </label>
            </Button>
          </div>
        )}
      </div>

      {/* Items List */}
      <div className="flex-1 overflow-y-auto p-4">
        {items.length === 0 ? (
          <div className="flex h-full items-center justify-center text-center text-gray-500">
            <div>
              <p className="mb-2 text-sm">No items yet</p>
              <p className="text-xs">
                {inputMode === "manual"
                  ? 'Click "Add New Item" to get started'
                  : 'Upload a STEP file to get started'}
              </p>
            </div>
          </div>
        ) : (
          <div className="space-y-3">
            {items.map((item) => (
              <div
                key={item.id}
                className={`rounded-lg border bg-white p-3 transition-all ${selectedItem === item.id ? "border-blue-400 shadow-md" : "border-gray-200"
                  }`}
              >
                <div className="mb-3 flex items-center gap-2">
                  <input
                    type="color"
                    value={item.color}
                    onChange={(e) => updateItem(item.id, "color", e.target.value)}
                    className="h-8 w-8 cursor-pointer rounded border border-gray-300"
                  />
                  <Input
                    value={item.name}
                    onChange={(e) => updateItem(item.id, "name", e.target.value)}
                    className="flex-1"
                    placeholder="Item name"
                  />
                  <Button
                    variant="ghost"
                    size="icon"
                    className="h-8 w-8"
                    onClick={() => setSelectedItem(item.id)}
                  >
                    <Check className={`h-4 w-4 ${selectedItem === item.id ? "text-blue-500" : "text-gray-400"}`} />
                  </Button>
                </div>

                {/* Orientation Options */}
                <div className="mb-3 flex gap-2">
                  <Button
                    variant="outline"
                    size="icon"
                    className={`h-9 w-9 ${item.stackable ? "bg-blue-50 border-blue-300" : ""}`}
                    onClick={() => updateItem(item.id, "stackable", !item.stackable)}
                    title="Stackable"
                  >
                    <div className="flex flex-col gap-0.5">
                      <div className="h-1 w-4 bg-current" />
                      <div className="h-1 w-4 bg-current" />
                    </div>
                  </Button>
                  <Button
                    variant="outline"
                    size="icon"
                    className={`h-9 w-9 ${item.turnable ? "bg-blue-50 border-blue-300" : ""}`}
                    onClick={() => updateItem(item.id, "turnable", !item.turnable)}
                    title="Turnable"
                  >
                    <RotateCcw className="h-4 w-4" />
                  </Button>
                  <Button
                    variant="outline"
                    size="icon"
                    className={`h-9 w-9 ${item.tiltable ? "bg-blue-50 border-blue-300" : ""}`}
                    onClick={() => updateItem(item.id, "tiltable", !item.tiltable)}
                    title="Tiltable"
                  >
                    <div className="h-4 w-4 rotate-45 border-2 border-current" />
                  </Button>
                </div>

                {/* Dimensions */}
                <div className="mb-3 grid grid-cols-3 gap-2">
                  <div>
                    <label className="mb-1 block text-xs text-gray-600">Length(cm)</label>
                    <Input
                      type="number"
                      value={item.length}
                      onChange={(e) => updateItem(item.id, "length", Number(e.target.value))}
                      className="h-9"
                    />
                  </div>
                  <div>
                    <label className="mb-1 block text-xs text-gray-600">Width(cm)</label>
                    <Input
                      type="number"
                      value={item.width}
                      onChange={(e) => updateItem(item.id, "width", Number(e.target.value))}
                      className="h-9"
                    />
                  </div>
                  <div>
                    <label className="mb-1 block text-xs text-gray-600">Height(cm)</label>
                    <Input
                      type="number"
                      value={item.height}
                      onChange={(e) => updateItem(item.id, "height", Number(e.target.value))}
                      className="h-9"
                    />
                  </div>
                </div>

                {/* Weight and Count */}
                <div className="flex gap-2">
                  <div className="flex-1">
                    <label className="mb-1 block text-xs text-gray-600">Weight(kg)</label>
                    <Input
                      type="number"
                      value={item.weight}
                      onChange={(e) => updateItem(item.id, "weight", Number(e.target.value))}
                      className="h-9"
                    />
                  </div>
                  <div className="w-24">
                    <label className="mb-1 block text-xs text-gray-600">Count</label>
                    <div className="flex items-center gap-1">
                      <Input
                        type="number"
                        value={item.count}
                        onChange={(e) => updateItem(item.id, "count", Math.max(0, Number(e.target.value)))}
                        className="h-9"
                      />
                      <div className="flex flex-col gap-0.5">
                        <Button
                          variant="ghost"
                          size="icon"
                          className="h-4 w-4 p-0"
                          onClick={() => updateItem(item.id, "count", item.count + 1)}
                        >
                          ▲
                        </Button>
                        <Button
                          variant="ghost"
                          size="icon"
                          className="h-4 w-4 p-0"
                          onClick={() => updateItem(item.id, "count", Math.max(0, item.count - 1))}
                        >
                          ▼
                        </Button>
                      </div>
                    </div>
                  </div>
                  <div className="flex items-end">
                    <Button
                      variant="ghost"
                      size="icon"
                      className="h-9 w-9 text-red-500 hover:bg-red-50 hover:text-red-600"
                      onClick={() => deleteItem(item.id)}
                      title="Delete item"
                    >
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  )
}